.recommendation-wall::-webkit-scrollbar {
  display: none;
  scrollbar-width: none;
  -ms-overflow-style: none; }

.recommendation-card {
  min-width: 350px; }

.recommendation-card:not(:last-child) {
  margin-right: 30px; }

header a[data-scroll="true"] {
  @apply text-primary; }

[data-cartMsg="false"] {
  @apply hidden; }

[data-cartMsg="true"] {
  @apply table; }

[data-popUp="true"] {
  @apply flex; }

[data-popUp="false"] {
  @apply hidden; }
